import HttpUtil from '@/common/HttpUtil'

const indexApi = {
  /**
   * 网站统计
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getWebStaticInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/datareport/showtotal'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 拿到询盘信息
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getInquireInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/msg/product/list'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 精准推荐信息
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getRecommendInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/purchaser/recommendation/list'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 盈店学堂信息
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getSchoolInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/article/page'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 盈店学堂详情
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getSchoolDetailInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/article/find'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 生日信息
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  getBirthdayInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/contacts/birthdayprompt/list'
    // let postUrl = '/static/mock/indexBirthdayInfo.json'
    return HttpUtil.get(postUrl, params)
  },
  /**
   * 删除生日信息
   * @param  {[type]} params [description]
   * @return {[type]}        [description]
   */
  removeBirthdayInfo (params) {
    let postUrl = process.env.DOMAIN + '/bshop/contacts/birthdayprompt/remove'
    return HttpUtil.get(postUrl, params)
  }
}
export default indexApi



